* {
  box-sizing: border-box; }

main {
  display: block; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace, monospace;
  font-size: 1em; }

a {
  background-color: transparent; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted; }

b,
strong {
  font-weight: bolder; }

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: 0.35em 0.75em 0.625em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  vertical-align: baseline; }

textarea {
  overflow: auto; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details {
  display: block; }

summary {
  display: list-item; }

template {
  display: none; }

[hidden] {
  display: none; }

.container {
  max-width: 1023px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 10px; }
  @media (min-width: 767px) {
    .container {
      padding: 0 20px; } }

.containerBike {
  max-width: 1210px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 10px; }
  @media (min-width: 767px) {
    .containerBike {
      padding: 0 20px; } }

.containerMain {
  max-width: 1244px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 10px; }
  @media (min-width: 767px) {
    .containerMain {
      padding: 0 20px; } }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"] .visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Light.woff") format("woff"), url("../fonts/OpenSans-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Regular.woff") format("woff"), url("../fonts/OpenSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-SemiBold.woff") format("woff"), url("../fonts/OpenSans-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap; }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/OpenSans-Bold.woff") format("woff"), url("../fonts/OpenSans-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap; }

.f-basicLight-menu {
  font-size: 2rem;
  font-weight: 400;
  color: #48494d; }
  @media (min-width: 1023px) {
    .f-basicLight-menu {
      font-weight: 300;
      font-size: 1.6rem;
      color: white; } }

.f-basicRegular,
.f-basicRegular-Small {
  font-weight: 400;
  line-height: 1.87; }

.f-basicRegular-Small {
  font-size: 1.5rem;
  color: #1c1c1c; }

.f-basicRegular-Small3 {
  font-size: 1.6rem;
  color: #808080; }

.f-basicRegular {
  font-size: 1.8rem;
  color: black; }
  @media (min-width: 767px) {
    .f-basicRegular {
      font-size: 2.2rem;
      line-height: 1.34; } }

.f-basicSemiBold-Small2,
.f-basicSemiBold-Middle,
.f-basicSemiBold-Middle2,
.f-basicSemiBold-Middle3 {
  font-weight: 600;
  color: black;
  line-height: 1.21; }

.f-basicSemiBold-Small2 {
  font-size: 2.2rem;
  line-height: 1.34;
  letter-spacing: -0.5px; }

.f-basicSemiBold-Middle {
  font-size: 2.4rem;
  line-height: 1.41;
  color: #1f1f1f; }
  @media (min-width: 1023px) {
    .f-basicSemiBold-Middle {
      font-size: 3rem;
      line-height: 1.3; } }

.f-basicSemiBold-Middle2 {
  font-size: 2.4rem;
  line-height: 1.3;
  color: white; }
  @media (min-width: 1023px) {
    .f-basicSemiBold-Middle2 {
      font-size: 3rem; } }

.f-basicSemiBold-Middle3 {
  font-size: 1.8rem;
  line-height: 1.3;
  color: #1f1f1f; }
  @media (min-width: 1023px) {
    .f-basicSemiBold-Middle3 {
      font-size: 3rem; } }

.f-basicBold {
  font-weight: 700;
  font-size: 3rem;
  color: white;
  line-height: 1.42; }
  @media (min-width: 1023px) {
    .f-basicBold {
      font-size: 4.6rem;
      line-height: normal; } }
